
<!-- 设置value后 change不生效 -->
<el-checkbox-group v-bind="$attrs" v-on="$listeners" :value="checkboxValue" @change="handleChange"  >
  <el-checkbox
    v-for="item in options"
    :key="item.value"
    :label="item.value"
    :checked="!!item.checked"
    :disabled="!!item.disabled"
  >
    {{item.label}}
  </el-checkbox>
</el-checkbox-group>
